<template>
  <div class="container">
    <skeleton :nums="4" :iheight="'.4rem'" v-if="isSkeletonLoading"/>
    <div v-else>
      <div ref="contentContainer" class="content-container">
        <div class="icon-back" @click="goBack" v-if="!$route.query.browerFirstIn">
          <img :src="$globalIcon.whiteBackIcon"/>
        </div>
        <div v-if="isUplus || isFromRN || isFromIhaier" @click="doShare" class="share-btn">
          <img alt :src="$globalIcon.whiteShareIcon" />
        </div>
        <van-grid :border="false" :column-num="2">
          <van-grid-item
            v-for="(item, index) in dataList"
            @click="imgClick(item)"
            :key="index">
            <van-image
              lazy-load
              width='50vw'
              :src="item.imageUrl.replace('http:', 'https:') | imageToWebp($webpSurpport)" />
          </van-grid-item>
        </van-grid>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import bannerService from '@/http/service/banner';
import Skeleton from '@/components/skeleton/index.vue';
import utils from '@/utils/utils';
import config from '../../config';
const localData = {
  activityInfo: {},
  isHost: 0,
  storeId: '',
  scrollTopMap: new Map(),
};
export default {
  name: 'bannerTheme',
  components: {
    Skeleton,
  },
  data() {
    return {
      isSkeletonLoading: true,
      isUplus: false,
      isFromRN: false,
      isFromIhaier: this.$util.isIhaierApp(),
      dataList: [],
    };
  },
  watch: {
    // 如果路由有变化，会再次执行该方法
    '$route.path': function(newVal, oldVal) {
      if (newVal.includes('bannerTheme')) {
        this.isWdHost();
      }
    }
  },
  beforeRouteEnter (to, from, next) {
    if (utils.isSgRn()) {
      const bannerThemeId = to.params.id;
      const storeId = to.query.storeId;
      window.postMessage(
        JSON.stringify({
          type: 'goBannerTheme',
          data: {
            bannerThemeId,
            storeId,
          },
        })
      )
      next(false)
    } else {
      next();
    }
  },
  mounted() {
    this.$util.gioTrackWithVariable('mall_activity_view', {
      activity_id: this.$route.query.id,
      activity_type: '主题活动'
    })
    this.isUplus = this.$util.isHaierApp();
    this.isFromRN = location.search.indexOf('sg_rn_app') !== -1;
    console.log('mounted--数据');
    this.isWdHost();

    // 活动浏览时长统计
    if (this.$route.query.taskCode) {
      this.$store.dispatch('doReviewTime', this.$route.query).then(res => {
        res && this.$toast('已达成浏览时长')
      })
    }
  },
  activated() {
    let top = localData.scrollTopMap.get(this.$route.params.id);
    this.$nextTick(() => {
      !!this.$refs.contentContainer.scrollTo && this.$refs.contentContainer.scrollTo(0, top || 0);
    });
  },
  computed: {
    ...mapGetters(['mId'])
  },
  methods: {
    doShare() {
      const title = localData.activityInfo.title; // 分享标题
      const content = localData.activityInfo.content || '海尔智家商城'; // 分享内容
      const pic = localData.activityInfo.imgUrl; // 分享图片，写绝对路径
      let url = config.HOST + '/sgmobile/bannerSubject?id=' + this.$route.params.id + '&shareId=' + this.mId;
      // let url = location.href.indexOf('?') !== -1 ? location.href + '&shareId=' + this.mId : location.href + '?shareId=' + this.mId
      if (this.isFromRN) {
        window.postMessage('custPageShare|' + title + '|' + content + '|' + pic + '|' + url);
      } else if (this.$util.isHaierApp() || this.$util.isIhaierApp()) {
        this.$util.inAppShare({
          title,
          url,
          desc: content,
          thumImage: pic,
        }).then(res => console.log(res))
      }
    },
    goBack() {
      // this.$router.back();
      localData.scrollTopMap.delete(this.$route.params.id);
      this.$store.dispatch('doHistoryBack');
      this.dataList = [];
    },
    isWdHost(cb) {
      bannerService.isWdHost().then(res => {
        console.log('isWdHost:', res.data);
        if (res.data.success) {
          localData.isHost = res.data.data.isHost;
          localData.storeId = res.data.data.storeMemberId;
          this.getActivityPage(cb);
        }
      }).catch(err => {
        console.log(err);
        localData.isHost = 0;
        localData.storeId = 0;
        this.getActivityPage();
      });
    },
    getActivityPage (cb) {
      let id = this.$route.params.id;
      bannerService.getActivityPage({
        bannerId: id,
        storeId: localData.storeId,
        isHost: localData.isHost,
        backUrl: ''
      }).then(res => {
        console.log(res.data);
        if (res.data.success) {
          this.isSkeletonLoading = false;
          localData.activityInfo = res.data.data
          this.dataList = res.data.data.productList;
          let top = localData.scrollTopMap.get(id);
          setTimeout(() => {
            this.$refs.contentContainer.scrollTo(0, top || 0);
          }, 500);
        }
        cb && cb()
        // 二次分享
        this.$store.dispatch('WeChatSecondShare', {
          title: localData.activityInfo.title,
          desc: localData.activityInfo.content,
          thumImage: localData.activityInfo.imgUrl,
        })
      });
    },
    imgClick(item) {
      console.log('item：', item);
      localData.scrollTopMap.set(this.$route.params.id, this.$refs.contentContainer.scrollTop);
      this.$store.dispatch('goPageByType', {
        type: Number(item.activityUrlType),
        params: item.activityUrl,
        elementId: item.id
      })
    },
  },
}
</script>
