  position: relative;
  background-color: white;
  height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}
.content-container {
  position: relative;
  background-color: white;
  height: 100vh;
  display: flex;
  overflow: scroll;
  -webkit-overflow-scrolling: touch;
  flex-direction: column;
}
.icon-back,.share-btn{
  position: fixed;
  width: 0.64rem;
  height: .64rem;
  top: 0.32rem;
  z-index: 10;
  background-color: rgba(0,0,0,.3);
  border-radius: 50%;
  box-sizing: border-box;
  padding: 0.14rem;
   img{
    -webkit-touch-callout: none;
    pointer-events: none;
  }
}
